import React, {Component} from 'react';
import Phonebook from './phonebook/Phonebook';
import Contacts from './contacts/Contacts'
import Filter from './filter/Filter'

class App extends Component {
  state = { 
      contacts: [],
      filter: ''
   }

  getValue = value => {
    console.log(value);
    this.setState(prev => ({
      contacts: [value, ...prev.contacts]
    }));
  };

  filter = e => {
    this.setState({
      filter: e.target.value
    });
  };
  
  getFilteredContacts = () => {
    return this.state.contacts.filter(contact =>
      contact.name.toLowerCase().includes(this.state.filter)
    );
  };

  deleteContact = e => {
    const id = e.target.id;
    this.setState(prevState => ({
      contacts: prevState.contacts.filter(contact => contact.id !== id)
    }));
  };
  //  componentDidUpdate(prevProps, prevState) {
  //   console.log("prevState", prevState);
  //   console.log("this.state", this.state);
  //   return this.state.contacts.filter(contact => contact.name.toLowerCase().includes(this.state.filter))
  // }
  
  render() {
    return (
      <>
      <h2>Phonebook</h2>
      <Phonebook {...this.state} onGetValue={this.getValue}/>
      
      <h2>Contacts</h2>
      <Filter filter={this.filter} />
      
      <Contacts contacts={this.getFilteredContacts()}
        deleteContact={this.deleteContact}/>
      
      </>
    );
  }
}

export default App;